body {
  min-height: calc(100vh - 155px);
  width: 100%;
  overflow-x: hidden;
  margin-bottom: 80px;
  margin-top: 75px;
  background: url('./bg.jpg');
  background-size: cover;
}

nav a {
  color: white;
  text-decoration: none;
}
nav a:hover {
  color: rgba(255,255,255,.5);
}

.vertical-timeline {
  overflow-x: hidden;
}

.scribbleContainer .button {
  display: inline-block; 
  font-family: sans-serif;
  color: #296eb2;
  text-decoration: none;
  font-size: 20px;
  line-height: 1;
  font-weight: 700; 
  text-align: center;
  -webkit-transition: all .5s;
  transition: all .5s;
  cursor: pointer;
}
.scribbleContainer .button:hover {
  color: #2b2742;
}
.scribbleContainer .button.scribble {
  padding: 18px 20px 30px 33px;
}

.scribbleContainer { 
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.scribbleContainer svg { 
  position: absolute; 
  top: 0; 
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.scribbleContainer .scribblePath { 
  fill: none; 
  stroke: #296eb2; 
  stroke-width: 3;
  transition: stroke .5s;
}
.scribbleContainer:hover .scribblePath {
  stroke: #2b2742;
  stroke-dasharray: 100px 1900px;
  animation: dashoffset 4s linear forwards infinite;
}

@keyframes dashoffset {
    from {stroke-dashoffset: 2000px;}
    to {stroke-dashoffset: 0px;}
}

.react-tabs__tab.react-tabs__tab--selected .scribblePath {
  stroke: #2b2742;
  stroke-dasharray: 100px 1900px;
  animation: dashoffset 4s linear forwards infinite;
}
.react-tabs__tab-list {
  display: flex;
  justify-content: center;
}
.react-tabs__tab {
  width: 100px;
  background: none !important;
  border: none !important;
}


.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
